import service from "auth/FetchInterceptor";
import API_CONSTANTS from "constants/ApiConstant";
import Utils from "utils";
import { HEADERS_NAME_FOR_EXPORT } from "constants/CommonConstants";

const targetDataReportService = {};

targetDataReportService.preparedHeadersForPDF = (data = []) =>
  data.map(col => {
    return { key: col.dataIndex, value: HEADERS_NAME_FOR_EXPORT[col.dataIndex] };
  });

targetDataReportService.preparedDataForExport = (data = [], filterVisibleColumns, forExcel = true) => {
  let headers = {};
  let _data = data.map(record => {
    const _item = {
      region: record?.region || "-",
      accountName: record?.accountName || "-",
      totalBillable: record?.totalBillable || "-",
    };
    if (filterVisibleColumns) {
      Object.keys(_item).forEach(itemKey => {
        if (!filterVisibleColumns[itemKey]) {
          delete _item[itemKey];
        }
      });
    }
    if (forExcel) headers = Utils.makeHeaderDataForExcel(_item);
    return _item;
  });
  if (forExcel) _data.unshift(headers);
  return _data;
};

targetDataReportService.flatResponse = response => {
  return response?.map(record => {
    return {
      region: record?.region || "",
      totalBillable: record?.totalBillable || "",
      accountName: record?.accountName || "-",
    };
  });
};

targetDataReportService.getReport = async body => {
  const { path, method } = API_CONSTANTS.GET_TARGET_DATA_REPORT;
  try {
    const response = await service({
      url: path,
      method,
      data: body,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response;
  } catch (e) {
    console.error("Error in getting the target data list.", e);
  }
};

targetDataReportService.getReportGraphs = async body => {
  const { path, method } = API_CONSTANTS.GET_TARGET_DATA_REPORT_GRAPHS;
  try {
    const response = await service({
      url: path,
      method,
      data: body,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response.records;
  } catch (e) {
    console.error("Error in getting the target data graphs.", e);
  }
};

export default targetDataReportService;
